
nav#app {
    position: absolute;
}
h2 {
  font-size: 14px;
  color: lightgray;
}
div.park { 
    text-align:center;
    font-family: Arial, Helvetica, sans-serif;
    color: darkmagenta;
}
p {
    display: inline;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    color: darkmagenta;
}
nav#navMenu {
    text-align: left;
}
p.regular {
    color:gray;
    font-size: 20px;
}
p.tiny {
    color:gray;
    font-weight: bold;
    font-size: 20px;
}
circle {
    fill: lightgray;
    stroke: #322F41;
    stroke-width: 5;
}
text {
    font-family: Arial, Helvetica, sans-serif;
    fill: darkmagenta;
    font-size: 30px;
}
.container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.left {
    -webkit-box-flex: -5;
        -ms-flex: -5 0 65%;
            flex: -5 0 65%;
}
.right {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    -webkit-box-align: left;
        -ms-flex-align: left;
            align-items: left;
}
option {
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
}
select {

    padding: 16px 20px;
    border: none;
    border-radius: 4px;
    background-color: #f1f1f1;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}
button {
    border-radius: 5px;
    background-color: purple; /* Green */
    border: thin;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.button:hover {
    background-color: #4CAF50; /* Green */
    color: white;
}



